// General
//------------------------------------------------------------------------------
* {
  margin: 0;
  padding: 0;
}

li {
  list-style-type: none;
}

// Posts list
//------------------------------------------------------------------------------
.posts_list {
  display: flex;
  flex-direction: column;
  margin: auto;
  max-width: 1024px;
}

.posts_list .entry {
  padding: 11px 0px 10px 0px;
  display: flex;
  border-style: dashed;
  border-width: 0px 0px 1px 0px;
  border-color: #c0c0c0;
}

// Temporary
//------------------------------------------------------------------------------
.current {
  color: green;
}

.thumbnail {
  width: 128px;
  height: 128px;
}